<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
import { getLandDomain } from '@/api/main'
export default {
  name: 'App',
  data () {
    return {
      
    }
  },
  created(){},
  mounted() {
    this.$global.appendChild()
    this.handleState()
  },
  methods: {
    handleState(){
      var ths = this
      history.pushState( null, null, '#/');
      window.addEventListener('popstate', function(event) {
        ths.handleGetAd()
      })
    },
    handleGetAd(){
       getLandDomain().then(response => {
        var data = response.data
        window.location.href = data.url + "#/ad/"
      })        
    },
  },
  watch: {
    $route() {
      this.$global.routeWatch()
    }
  }
}
</script>

<style>
@import './assets/css/reset.css';
#app{
  font-size: 24px;
  height: 100%;
  background: #eee;
}
</style>



