import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

/* page */
const Share = () => import('@/views/share/share')

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Share',
      component: Share,
      meta:{
        title: '　　　'
      } 
    },   
    { path: '*', redirect: '/' }  
  ]
})



